import React, { useRef, useEffect } from "react";
import { useSelector } from 'react-redux';
import { sum } from 'lodash';
import useAuth from 'hooks/useAuth';

export default function PaypalButton({rows, onPost}) {
  const { user } = useAuth();
  const paypal = useRef();
  const cart = useSelector((state) => state.cart);
  const totalPrice = sum(cart.checkout.products.map((item) => item.price));

  const fireCompleteHandler = () => {
    onPost()
  }

  useEffect(() => {

    setTimeout(function(){ 

      console.log("CHECKOUT TOTAL:", cart.checkout)

      window.paypal
      .Buttons({
        createOrder: (data, actions, err) => {
          console.log('totalPrice', totalPrice)
          return actions.order.create({
            /* intent: "CAPTURE", */
            purchase_units: [
              {
                description: "Cool looking table",
                products: cart.checkout.products,
                amount: {
                  /* currency_code: "C", */
                  value: cart.checkout.total/* rows.offerPrice */,
                },
              },
            ],
          });
        },
        onApprove: async (data, actions, ) => {
          const order = await actions.order.capture();
          console.log(order);
          fireCompleteHandler()

          /* const response = await fetch('http://127.0.0.1:8000/api/add-order/', {
                    method: 'POST',
                    headers: {
                        'Content-Type':'application/json'
                    },
                    body: JSON.stringify({
                        'customer_name': 'test name', 
                        'quantity': 'test order',
                        'payment_type': 'test order',
                        'products': 'test order',
                        'product_owner': 'test order',
                        'payer_email': order.payer.email_address,
                        'payer_name': 'test order',
                        'payer_id': 'test order',
                        'purchase_amount': 45,
                        'shipping_address': 'test order',
                        'shipping_option': 'test order'
                    })

                    
                }) */

            /* console.log('from Order Tab pAypal Buttons', response) */
        },
        onError: (err) => {
          console.log(err);
        },
      })
      .render(paypal.current);

     }, 5000);

    
  }, []);

  return (
    <div>
      <div ref={paypal}></div>
    </div>
  );
}